.program__title {
  @include font(36px, 36px, $black, 400);
  font-family: $fontSecondary;
  margin: 0 0 37px 0;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
    margin: 65px 0 54px 40px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 56px;
  }
}

.program-form__container {
  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
  }
}
